<template>
  <div id="content">
    <div class="block" v-for="item in gameList" :key="item.name" :style="{backgroundColor: item.color}" @click="goto(item.path)">
      <div class="innerBlock">
        <div class="name">{{item.name}}</div>
        <div class="desc">{{item.desc}}</div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">
  #content {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    align-content: stretch;
    .block{
      min-width: 25%;
      flex: 1;
      cursor: pointer;
      .innerBlock{
        width: 100%;
        height: 100%;
        padding: 0 20px;
        box-sizing: border-box;
        background-color: rgba(0,0,0,0.5);
        transition: background-color 300ms;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        align-items: center;
        .name{
          color: rgba(255,255,255,.5);
          transition: color 500ms;
          font-size: 20px;
          font-weight: bold;
        }
        .desc{
          margin-top: 10px;
          color: rgba(255,255,255,.25);
          transition: color 500ms;
          font-size: 12px;
        }
      }
      .innerBlock:hover{
        background-color: rgba(0,0,0,0);
        .name{
          color: rgba(0,0,0,.75);
        }
        .desc{
          color: rgba(0,0,0,.5);
        }
      }
    }
  }
</style>

<script>
export default {
  name: 'gameMenu',
  data () {
    return {
      gameList: [
        {
          name: "扫雷",
          path: "/minesweeper",
          desc: "经典扫雷，左键打开，右键插旗",
          color: "#b0b0b0",
        },
        {
          name: "贪吃蛇",
          path: "/snake",
          desc: "贪吃蛇，WASD或方向键操控",
          color: "#707070",
        },
        {
          name: "填色",
          path: "/color",
          desc: "填色使之成渐变状",
          color: "#ff5e52",
        },
        {
          name: "连连看",
          path: "/link",
          desc: "经典连连看，左键操作，无解需要刷新重来",
          color: "#ffef6e",
        },
        {
          name: "跳一跳",
          path: "/jump",
          desc: "横版跳跃游戏，WASD或方向键操控",
          color: "#6cd7ff",
        },
        {
          name: "Picross",
          path: "/picross",
          desc: "经典Picross",
          color: "#5affb6",
        },
        {
          name: "猜数字",
          path: "/digital",
          desc: "经典猜数字游戏 - Bulls and Cows",
          color: "#e19bff",
        },
        {
          name: "狼人杀上帝助手",
          path: "/wolf",
          desc: "狼人kill专用发牌器",
          color: "#ffbc64",
        },
      ]
    }
  },
  created(){
    //禁止右键菜单
    document.oncontextmenu = function(){
      event.returnValue = false;
    };
    //禁止选择文本
    document.onselectstart = function(){
      event.returnValue = false;
    };
  },
  methods: {
    goto(url){
      this.$router.push(url);
    }
  }
}
</script>



